form {
    display: grid;
    justify-items: center;
}

label {
    font-size: 36px;
    color: var(--white);
    text-align: center;
    margin-bottom: 12px;
}



form input {
    height: 36px;
    width: 200px;
    border: none;
    border-radius:8px ;
    margin-top: 8px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    font-size: 16px;
    text-align: center;
}

form select {
    height: 36px;
    width: 200px;
    border: none;
    border-radius:8px ;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    text-align: center;
}

form div {
    display: flex;
    justify-content: center; 
    gap: 8px;
    margin-top: 10px;
}

form button {
    padding: 10px 24px;
    font-size: 16px;
    border: none; 
    border-radius: 8px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

form button[type="button"] {
    background-color: var(--balance);
}

form button[type="button"]:hover {
    background-color: #f80909fa;
    color: var(--white);
}

form button[type="submit"] {
    background-color: var(--send);
}

form button[type="submit"]:hover {
    background-color: #0a74ff;
    color: var(--white);
}