.ControlItem{
    display: flex;
    color: var(--black);
    width: 80vw;
    height: 18vh;
    margin: 8px;
    margin-bottom: 0;
    border: 4px solid var(--black);
    border-radius: 8px;
    font-size: 36px;
    list-style: none;
    align-items: center;
}

.ItemControl {
    display: flex;
    align-items: center;
    margin: 32px;
    flex: 1;
    justify-content: space-between;
}

li {
    box-shadow: 0px 4px 6px rgba(204, 3, 3, 0.219);
}

.item-income {
    background-color: var(--income);
    
}

.item-expensive {
    background-color: var(--expensive)
}

.item-saving {
    background-color: var(--saving);
}

.item-balance {
    background-color: var(--balance);
}

.IconMain {
    font-size: 48px;
}